export default {
	data() {
		return {
			flag: 0,
			text: '',
			lastX: 0,
			lastY: 0,
			tabIndex: false
		}
	},
	methods: {
		deleteIcon(){
			uni.showModal({
			    title: '',
			    content: '是否删除地址信息',
				// mask: true,
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		handletouchmove: function(event) {
			// console.log(event)
			if (this.flag !== 0) {
				return;
			}
			let currentX = event.touches[0].pageX;
			let currentY = event.touches[0].pageY;
			let tx = currentX - this.lastX;
			let ty = currentY - this.lastY;
			let text = '';
			this.mindex = -1;
			//左右方向滑动
			if (Math.abs(tx) > Math.abs(ty)) {
				if (tx < 0) {
					text = '向左滑动';
					this.flag = 1;
					this.tabIndex = true
					// this.item.X = 120
					console.log('向左')
				//	this.getList();  //调用列表的方法
				} else if (tx > 0) {
					text = '向右滑动';
					this.tabIndex = false
					this.flag = 2;
				}
			}
			//上下方向滑动
			else {
				if (ty < 0) {
					text = '向上滑动';
					this.flag = 3;
				//	this.getList();  //调用列表的方法
				} else if (ty > 0) {
					text = '向下滑动';
					this.flag = 4;
				}
			}

			//将当前坐标进行保存以进行下一次计算
			this.lastX = currentX;
			this.lastY = currentY;
			this.text = text;
		},
		handletouchstart: function(event) {
			// console.log(event)
			this.lastX = event.touches[0].pageX;
			this.lastY = event.touches[0].pageY;
		},
		handletouchend: function(event) {
			this.flag = 0;
		},
	}
}
